.green {
  color: #4caf50 !important;
}

.red {
  color: #f44336 !important;
}

form {
  width: 80%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mdl-textfield {
  width: 100%;
}

.mdl-textfield__input, .mdl-textfield__label, .npc-result {
  letter-spacing: -.02em;
  text-align: center;
  outline: none;
  font-family: Roboto, Helvetica, Arial, sans-serif;
  font-size: 8vmin;
  line-height: 1;
}

.mdl-spinner {
  width: 20px;
  height: 20px;
}

@media only screen and (width >= 480px) {
  .mdl-spinner {
    width: 5vmin;
    height: 5vmin;
  }
}

.mdl-spinner, .npc-result {
  margin: 0 auto;
  position: absolute;
  left: 0;
  right: 0;
}

#npc-result-text, #npc-result-icon {
  color: #bbb;
  vertical-align: middle;
  font-size: 6vmin;
}

#npc-result-text {
  cursor: default;
  text-decoration: none;
}

#npc-result-text.hover:hover {
  color: #aaa;
  cursor: pointer;
  border-bottom: 1px solid;
  text-decoration: none;
}
